import React, { Component, createContext } from "react";
import { useFetch } from "./useFetch";
export const Context = createContext();

function Provider(props) {
  const gloves = useFetch(
    `https://erkanisufreaktor.herokuapp.com/products/gloves/`
  );
  const facemasks = useFetch(
    `https://erkanisufreaktor.herokuapp.com/products/facemasks/`
  );
  const beanies = useFetch(
    `https://erkanisufreaktor.herokuapp.com/products/beanies/`
  );

  return (
    <Context.Provider
      value={{
        facemasks: facemasks.error ? [] : facemasks.data,
        gloves: gloves.error ? [] : gloves.data,
        beanies: beanies.error ? [] : beanies.data,
      }}
    >
      {props.children}
    </Context.Provider>
  );
}
export default Provider;
